<template>
  <el-row class="container">
    <!---->
    <el-col :span="24" class="topbar-wrap">
      <div class="topbar-logo topbar-btn">
        <a href="/">
          <img src="../assets/logo.png" style="padding-left:8px;">
        </a>
      </div>
      <div class="topbar-logos" v-show="!collapsed">
        <a href="/">
          <img src="../assets/logotxt.png">
        </a>
      </div>
      <div class="topbar-title">
        <span style="font-size: 18px;color: #fff;"></span>
      </div>
      <div class="topbar-account topbar-btn">
        <el-dropdown trigger="click">
          <span class="el-dropdown-link userinfo-inner">
            <i class="iconfont icon-user"></i>
            {{nickname}}
            <i class="iconfont icon-down"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>
              <div @click="jumpTo('/user/profile')">
                <span style="color: #555;font-size: 14px;"></span>
              </div>
            </el-dropdown-item>
            <el-dropdown-item>
              <div @click="jumpTo('/user/changepwd')">
                <span style="color: #555;font-size: 14px;"></span>
              </div>
            </el-dropdown-item>
            <el-dropdown-item divided @click.native="logout"></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-col>

    <!---->
    <el-col :span="24" class="main">
      <!---->
      <aside :class="{showSidebar:!collapsed}">
        <!---->
        <div class="menu-toggle" @click.prevent="collapse">
          <i class="fa fa-bars" v-show="!collapsed"></i>
          <i class="fa fa-bars fa-rotate-90" v-show="collapsed"></i>
        </div>
        <!---->
        <el-menu :default-active="defaultActiveIndex" :collapse="collapsed" @select="handleSelect">
          <template v-for="(item,index) in menus" v-if="item.menuShow">
            <el-submenu v-if="!item.leaf" :index="index+''">
              <template slot="title">
                <i :class="item.iconCls"></i>
                <span slot="title">{{item.name}}</span>
              </template>
              <el-menu-item
                v-for="term in item.children"
                :key="term.path"
                :index="term.path"
                v-if="term.menuShow"
                :class="$route.path==term.path?'is-active':''"
              >
                <i :class="term.iconCls"></i>
                <span slot="title">{{term.name}}</span>
              </el-menu-item>
            </el-submenu>
            <el-menu-item
              v-else-if="item.leaf&&item.children&&item.children.length"
              :index="item.children[0].path"
              :class="$route.path==item.children[0].path?'is-active':''"
            >
              <i :class="item.iconCls"></i>
              <span slot="title">{{item.children[0].name}}</span>
            </el-menu-item>
          </template>
        </el-menu>
      </aside>

      <!---->
      <!---->
      <section
        class="content-container"
        v-loading="$store.state.loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(255, 255, 255, 0)"
      >
        <div class="grid-content bg-purple-light">
          <el-col :span="24" class="content-wrapper">
            <transition name="fade" mode="out-in">
              <router-view></router-view>
            </transition>
          </el-col>
        </div>
      </section>
    </el-col>
  </el-row>
</template>

<script>
import { bus } from "../bus.js";
import API from "../api/api_user";

export default {
  name: "home",
  created() {
    bus.$on("setNickName", text => {
      this.nickname = text;
    });

    bus.$on("goto", url => {
      if (url === "/login") {
        localStorage.removeItem("access-user");
      }
      this.$router.push(url);
    });
    this.defaultActiveIndex = this.$route.path;
  },
  data() {
    return {
      defaultActiveIndex: "0",
      nickname: "",
      collapsed: false,
      menus: []
    };
  },
  methods: {
    handleSelect(index) {
      if (index.indexOf("http") != -1) {
        window.open(index);
      } else {
       this.$router.push(index);
      }
    },
    //
    collapse: function() {
      this.collapsed = !this.collapsed;
    },
    jumpTo(url) {
      if (url.indexOf("http") != -1) {
      } else {
        this.defaultActiveIndex = url;
        this.$router.push(url); //go
      }
    },
    logout() {
      let that = this;
      this.$confirm("?", "", {
        confirmButtonClass: "el-button--warning"
      })
        .then(() => {
          //
          that.loading = true;
          //
          localStorage.removeItem("access-token");
          localStorage.removeItem("menus");
          API.logout("").then(function(res) {
            that.$message.error({
              showClose: true,
              message: res.msg,
              duration: 2000
            });
          });
          that.$router.go("/login"); //go
        })
        .catch(() => {});
    }
  },
  mounted() {
    this.menus = JSON.parse(window.localStorage.getItem("menus"));
    // if (user) {
    //   user = JSON.parse(user);
    //   this.nickname = user.nickname || '';
    // }
    let that = this;
    API.tokenUser()
      .then(function(result) {
        that.nickname = result.username;
      })
      .catch(() => {
        localStorage.removeItem("access-token");
        that.$router.go("/login"); //go
      });
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;

  .topbar-wrap {
    height: 50px;
    line-height: 50px;
    background: #373d41;
    padding: 0px;

    .topbar-btn {
      color: #fff;
    }
    /*.topbar-btn:hover {*/
    /*background-color: #4A5064;*/
    /*}*/
    .topbar-logo {
      float: left;
      width: 59px;
      line-height: 26px;
    }
    .topbar-logos {
      float: left;
      width: 120px;
      line-height: 26px;
    }
    .topbar-logo img,
    .topbar-logos img {
      height: 40px;
      margin-top: 5px;
      margin-left: 2px;
    }
    .topbar-title {
      float: left;
      text-align: left;
      width: 200px;
      padding-left: 10px;
      border-left: 1px solid #000;
    }
    .topbar-account {
      float: right;
      padding-right: 12px;
    }
    .userinfo-inner {
      cursor: pointer;
      color: #fff;
      padding-left: 10px;
    }
  }
  .main {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    top: 50px;
    bottom: 0px;
    overflow: hidden;
  }

  aside {
    min-width: 50px;
    background: #2f4050;
    &::-webkit-scrollbar {
      display: none;
    }

    &.showSidebar {
      overflow-x: hidden;
      overflow-y: auto;
    }

    .el-menu {
      height: 100%; /*calc()*/
      height: -moz-calc(100% - 80px);
      height: -webkit-calc(100% - 80px);
      height: calc(100% - 80px);
      border-radius: 0px;
      background-color: #2f4050;
      border-right: 0px;
    }

    .el-submenu .el-menu-item {
      min-width: 60px;
    }
    .el-menu {
      width: 180px;
    }
    .el-menu--collapse {
      width: 60px;
    }

    .el-menu .el-menu-item,
    .el-submenu .el-submenu__title {
      height: 46px;
      line-height: 46px;
    }

    .el-menu-item:hover,
    .el-submenu .el-menu-item:hover,
    .el-submenu__title:hover {
      background-color: #293846;
      color: #fff;
    }
  }

  .menu-toggle {
    background: #4a5064;
    text-align: center;
    color: white;
    height: 26px;
    line-height: 30px;
  }

  .content-container {
    background: #fff;
    flex: 1;
    overflow-y: auto;
    padding: 10px;
    padding-bottom: 1px;

    .content-wrapper {
      background-color: #fff;
      box-sizing: border-box;
    }
  }
}
</style>



